export const confirmValue = data => {
  if (typeof data === "undefined") {
    return "";
  } else {
    return data;
  }
};

export const confirmString = str => {
  if (typeof str === "undefined") {
    return false;
  } else if (str.length === 0) {
    return false;
  } else {
    return true;
  }
};

export const confirmAuth = auth => {
  const { username, result, attempted } = auth;
  let user = confirmString(username);
  let resConfirm = result === "OK";
  return user && resConfirm && attempted;
};
